import { useEffect, useState } from 'react';
import { useDebaunce } from '../../hooks/debounce';
import { useAppDispatch } from '../../store';
import { pageChangeRating, searchChange } from '../../store/ratingStore/sliceRating/slice';
import './index.css';

const SearchUser = () => {
  const [search, setSearch] = useState('');
  const dispatch = useAppDispatch();
  const debounced = useDebaunce(search);

  useEffect(() => {
    dispatch(searchChange(debounced));
    dispatch(pageChangeRating(1));
  }, [debounced, dispatch]);

  return (
    <div className="search-form">
      <input
        className="search-user_input"
        type="text"
        value={search}
        placeholder=" "
        onChange={(e) => setSearch(e.currentTarget.value)}
      />
      {search && <div className="search-user_reser" onClick={() => setSearch('')}></div>}
    </div>
  );
};

export default SearchUser;
